@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.fpt-tracuu .container {
  background: #fff;
}
.fpt-tracuu .section-wrapper {
  padding: 72px 0 50px;
}
.fpt-tracuu .img {
  text-align: center;
}
@media (max-width: 992px) {
  .fpt-tracuu .img {
    margin-bottom: 30px;
  }
}
.fpt-tracuu .tracuu-form h1 {
  color: #333333;
  text-transform: uppercase;
  font-size: 25px;
  font-weight: 700;
  margin-left: 8.333333%;
  margin-bottom: 22px;
}
.fpt-tracuu .tracuu-form h1 span {
  display: block;
  color: #f26f21;
  text-transform: none;
  font-weight: 400;
}
@media (max-width: 992px) {
  .fpt-tracuu .tracuu-form h1 {
    margin-left: 0;
  }
}
.fpt-tracuu .search-container {
  margin-left: 8.333333%;
}
@media (max-width: 992px) {
  .fpt-tracuu .search-container {
    margin-left: 0;
  }
  .fpt-tracuu .search-container .wrap-form {
    margin-left: 0 !important;
  }
}
.fpt-tracuu .form-tabs ul {
  margin-left: 8.333333%;
  list-style: none;
  display: flex;
  margin-bottom: 32px;
}
.fpt-tracuu .form-tabs ul li + li {
  margin-left: 10px;
}
.fpt-tracuu .form-tabs ul li.active a, .fpt-tracuu .form-tabs ul li:hover a {
  background: #f26f21;
  color: #fff;
}
.fpt-tracuu .form-tabs ul li a {
  display: block;
  padding: 12px 35px;
  border: 1px solid #eea579;
  color: #f26f21;
  text-transform: uppercase;
  text-decoration: none;
}
@media (max-width: 1200px) {
  .fpt-tracuu .form-tabs ul li a {
    padding: 12px 20px;
  }
}
@media (max-width: 992px) {
  .fpt-tracuu .form-tabs ul {
    margin-left: 0;
  }
}
@media (max-width: 375px) {
  .fpt-tracuu .form-tabs ul li a {
    padding: 12px;
    font-size: 11.5px;
  }
}
.fpt-tracuu .form-tabs .wrap-form {
  font-family: "Muli";
}
.fpt-tracuu .form-tabs .wrap-form span {
  color: #f26f21;
  display: block;
  margin: 7px 0;
}
.fpt-tracuu .form-tabs .wrap-form .form-group {
  display: flex;
  align-items: center;
}
.fpt-tracuu .form-tabs .wrap-form .form-group label {
  flex: 0 0 25%;
}
.fpt-tracuu .form-tabs .wrap-form .form-group input {
  height: 40px;
  width: 440px;
  background: #f5f5f5;
  border: 1px solid #ebebeb;
  border-radius: 5px;
  padding-left: 14px;
}
@media (max-width: 992px) {
  .fpt-tracuu .form-tabs .wrap-form {
    margin-left: 8.333333%;
  }
  .fpt-tracuu .form-tabs .wrap-form .form-group input {
    width: 350px;
  }
}
@media (max-width: 768px) {
  .fpt-tracuu .form-tabs .form-group label {
    flex: 0 0 25% !important;
  }
  .fpt-tracuu .form-tabs .form-group input {
    width: 280px !important;
  }
}
.fpt-tracuu .btn-wrap {
  margin-top: 40px;
  display: flex;
}
.fpt-tracuu .btn-wrap:hover .btn-success {
  background: #00528e;
  transition: 0.3s all;
}
.fpt-tracuu .btn-success {
  display: block;
  padding: 12px 37px;
  background: #0066b2;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border: 0;
  font-family: "Roboto Slab";
  transition: 0.3s all;
}
@media (max-width: 576px) {
  .fpt-tracuu .btn-success {
    padding: 12px 28px;
  }
}
.fpt-tracuu .btn-append {
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #00528e;
  font-size: 25px;
}
.fpt-tracuu .btn-append span {
  color: #ffffff !important;
}
.fpt-tracuu .results {
  overflow-x: auto;
}
.fpt-tracuu table {
  border-collapse: separate;
  background: #fff;
  border-radius: 5px;
  margin: 50px auto;
}
.fpt-tracuu table thead {
  border-radius: 5px;
}
.fpt-tracuu table thead td {
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  text-align: left;
  padding: 20px;
  background: #f26f21;
}
.fpt-tracuu table thead td:first-child {
  border-top-left-radius: 5px;
}
.fpt-tracuu table thead td:last-child {
  border-top-right-radius: 5px;
}
.fpt-tracuu table tbody tr td {
  font-weight: 400;
  color: #5f6062;
  font-size: 14px;
  padding: 20px;
}
.fpt-tracuu table tbody tr:nth-child(2n) {
  background: #f0f3f5;
}
.fpt-tracuu table tbody tr:last-child td {
  border-bottom: 0;
}
.fpt-tracuu table tbody tr:last-child td:first-child {
  border-bottom-left-radius: 5px;
}
.fpt-tracuu table tbody tr:last-child td:last-child {
  border-bottom-right-radius: 5px;
}